<template>
    <div>
        <main>
            <section id="area-meio">
                <h1>Estudo Biblico</h1>
                <div class="card-lista">
                        <label class="input-label">Alunos</label>
                                    <div class="marcadores">
                                        <div v-for="(aluno,index) in l_estudo.alunos" :key="index">
                                            {{aluno}}
                                        </div>
                                    </div>
                </div>
                <div class="card-lista">
                        <label class="input-label">Professor</label>
                        <input type="text" class="input-form" name="professor" placeholder="Professor" v-model="l_estudo.professor" disabled>			
                </div>
                <div class="card-lista">
                    <label class="input-label">Num Aulas</label>
                    <input type="number" class="input-form" name="idade" placeholder="numero de aulas" min="0" v-model="l_estudo.aulas" disabled>			
                </div>
                <div class="card-lista">
                    <label class="input-label">Dt inicio</label>
                    <input type="date" class="input-form" name="idade" placeholder="data inicio" v-model="l_estudo.dt_inicio" disabled>			
                </div>
                <div class="card-lista">
                    <label class="input-label">Dt final previsivel</label>
                    <input type="date" class="input-form" name="idade" placeholder="data final previcivel" v-model="l_estudo.dt_final" disabled>			
                </div>
                <div class="card-lista-botoes">
                   <button @click="redirecionar" class="btn btn-cancel">Voltar</button>
                    <button @click="conf" class="btn btn-cancel">Configuração</button>
                </div>
            </section>
        </main>
    </div>
</template>
<script>
/* eslint-disable */
 import db from '@/store/percistencia_estudo';
    export default {
        name: 'ShowEstudos',
          data(){
                return {
                    n:'teste',
                    l_estudo:[]
                }
          },
          components:{
           
          },
          mounted(){
            this.listar();
          },
          methods:{
                redirecionar(){
                   this.$router.push(`/`);
                },
                conf(){
                   this.$router.push(`/estudo/conf`);
                },
                listar(){
                    this.l_estudo = db.lista();
                }
          }
    }
</script>
