<script>
// import "/css/principal_estilo.css";
 import percist from '@/store/percistencia';
 import db_lideranca from '@/store/percistencia_lideranca';

export default {
  name: 'PrincipalComp',
  data(){
    return {
        n:'teste',
        quantidade_total_desbravadores:0,
        quantidade_total_lideres:0,
        inicial:0,
        publicPath: process.env.BASE_URL
    }
  },
  methods:{
    redirecionar(){
        this.$router.push('/lista');
    },
    redirecionar_lideranca(){
        this.$router.push('/lideranca/listar')
    },
    redirecionar_estudos(){
        this.$router.push('/estudo/show')
    },
    redirecionar_tesouraria(){
        this.$router.push('/tesouraria/show')
    },
    redirecionar_almocharife(){
        this.$router.push('/almocharife/show')
    },
    redirecionar_atos(){
        this.$router.push('/atos/show')
    },
    carregar_metrica(){
        //quantidade total de desbravadores
        let lista = percist.lista();
        let lideranca = db_lideranca.lista();

        this.quantidade_total_desbravadores = lista.length;
        this.quantidade_total_lideres=lideranca.length;

        
    },
    aux_preencher_numero_crescente(num){
        let intervalo = 5000;
        let alvo_num = parseInt(num);
        let inicial_num = 0;
        let duracao = Math.floor(intervalo/alvo_num);
        let contador = setInterval(function(){
            inicial_num +=1;
            num.textContent = inicial_num;
            if(inicial_num == alvo_num){
                clearInterval(contador);
            }
        },duracao);


    },
    contador(){
      let final= parseInt(this.quantidade_total_desbravadores);
      final=20;  
        for (let index = 0; index < final; index++) {
            setTimeout((index)=>{
               this.quantidade_total_desbravadores = index; 
               console.log('dentro do time out',index);  
            },5000);
        }


    }

  },
  mounted(){
    this.carregar_metrica();
    this.contador();
  },
  beforeUnmount(){
    console.log('befor unmount');
  }
}

</script>
