/* eslint-disable */
export default {
    salvar(data){
        let tarefa = (localStorage.getItem('db_estudo'))?JSON.parse(localStorage.getItem('db_estudo')):[];
        tarefa =data;
        localStorage.setItem('db_estudo',JSON.stringify(tarefa));
    },
    lista(){
        let tarefa = (localStorage.getItem('db_estudo'))?JSON.parse(localStorage.getItem('db_estudo')):[];
        return tarefa;
    },
    pesquisar(texto){
        let tarefa = (localStorage.getItem('db_estudo'))?JSON.parse(localStorage.getItem('db_estudo')):[];
        let pesquisa =  tarefa.find((t)=>{
            return texto == t.nome;
        });
        if(texto === ""){
            pesquisa = tarefa;
        }
        return pesquisa;
    },
    getItem(id){
        let tarefa = JSON.parse(localStorage.getItem('db_estudo'));
        return tarefa[id];
    },
    updateItem(data,id){
        let tarefa = JSON.parse(localStorage.getItem('db_estudo'));
            tarefa[id]=data;
                localStorage.setItem('db_estudo',JSON.stringify(tarefa));
                    return {msg:'ok,mensagem',status:true};
    },
    removerItem(id){
        let tarefa = JSON.parse(localStorage.getItem('db_estudo'));
            tarefa.splice(id,1);
                localStorage.setItem('db_estudo',JSON.stringify(tarefa));
                    return {msg:'ok,mensagem',status:true};
    },
    pesqFuncAux(arr,texto){
        return arr.nome == `${texto}`;
    }
}