import { createRouter, createWebHistory } from 'vue-router'
// import HomeView from '../views/HomeView.vue'
import Principal from '../components/Principal.vue'
import Lista from '../components/ListaDbv.vue'
import Formulario from '../components/formComp.vue'
//liderança
import LidLista from '../components/lideranca/ListaDbv.vue'
import LidFormulario from '../components/lideranca/formComp.vue'
//estudo
import EstudoConf from '../components/estudos/formComp.vue'
import EstudoShow from '../components/estudos/showEstudos.vue'
//tesouraria
import TesourariaShow from '../components/tesouraria/showTesoraria.vue'
//almocharife
import AlmocharifeShow from '../components/almocharife/showAlmocharife.vue'
//atos
import AtosShow from '../components/atos/showAtos.vue'

const routes = [
  {
    path: '/',
    name: 'principal',
    component: Principal
  },
  {
    path: '/lista',
    name: 'lista',
    component: Lista
  },
  {
    path: '/form',
    name: 'form',
    component: Formulario
  },
  {
    path: '/form_edt/:id',
    name: 'form_edit',
    component: Formulario
  },
  {
    path: '/lideranca/listar',
    name: 'lid-listar',
    component: LidLista
  },
  {
    path: '/lideranca/form',
    name: 'lid-form',
    component: LidFormulario
  },
  {
    path: '/lideranca/form_edt/:id',
    name: 'lid-form-edt',
    component: LidFormulario
  },
  {
    path: '/estudo/conf',
    name: 'estudoConf',
    component: EstudoConf
  },
  {
    path: '/estudo/show',
    name: 'estudoShow',
    component: EstudoShow
  },
  {
    path: '/tesouraria/show',
    name: 'showTesoraria',
    component: TesourariaShow
  },
  {
    path: '/almocharife/show',
    name: 'showAlmocharife',
    component: AlmocharifeShow
  },
  {
    path: '/atos/show',
    name: 'showAtos',
    component: AtosShow
  },
  {
    path: '/about',
    name: 'about',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/AboutView.vue')
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
