<template>
            <section id="cabecalho">
                <nav id="form-topo">
                    <div class="campo-pesquisar">
                        <a href="">
                            <img  :src="`${publicPath2}/icons/search.svg`">
                        </a>
                        <input type="text" name="input-pesquisar" class="input-pesquisar" placeholder="Nome" v-model="tpesq"  v-on:keyup.enter="pesquisar(tpesq)"/>
                    </div>
                    <div class="btn-filtro" @click.prevent="pesquisar(tpesq)">
                        <a href="#">
                            <img :src="`${publicPath2}/icons/filter.svg`">
                        </a>
                    </div>
                    <div class="btn-add" @click="redirecionar()">
                        <span>
                            <img :src="`${publicPath2}/icons/plus.svg`">
                        </span>
                    </div>
                    <div @click="home()" style="cursor:pointer">
                        <span>
                            <img class="btn-home" :src="`${publicPath2}/icons/home.svg`" alt="home" title="home">
                        </span>
                    </div>
                </nav>
            </section>
</template>
<script>
/* eslint-disable */
 import db from '@/store/percistencia';
    export default {
        name: 'BarraFiltro',
        tpesq:'',
        emits:['pesqEvento'],
        data(){
            return {
                publicPath2: process.env.BASE_URL
            }
        },
        methods:{
            redirecionar(){
              this.$router.push(`/form`);
            },
            home(){
                this.$router.push(`/`);
            },
            pesquisar(texto){
                console.log('o resultado',db.pesquisar(texto));
                    let pesquisa_resultado = db.pesquisar(texto);
                        this.$emit('pesqEvento',pesquisa_resultado);
                            this.$router.push(`/lista`);
            }
        }
    }
</script>
<style lang="css">
    .btn-home{
        width: 50%;
    }
</style> 