<template>
    <div>
        <main>
            <filtro />
            <section id="area-meio">
                
                <div class="card-lista">
                        <label class="input-label">nome</label>
                        <input type="text" class="input-form" name="nome" placeholder="input form" v-model="form.nome">			
                </div>
                <div class="card-lista">
                    <label class="input-label">idade</label>
                    <input type="number" class="input-form" name="idade" placeholder="input form" v-model="form.idade">			
                </div>
                <div class="card-lista">
                    <label class="input-label">sexo</label>
                        <div class="cards-sexo">
                            <input type="radio" class="input-radio" id="masculino_radio" name="sexo" value="masculino"  v-model="form.sexo">
                                <label class="card-sexo" for="masculino_radio" @click="e => e.target.classList.toggle('labe-ativa')">Masculino</label>
                            <input type="radio" class="input-radio"  id="fem_radio" name="sexo" value="feminino"  v-model="form.sexo">
                                <label class="card-sexo"  for="fem_radio" @click="e => e.target.classList.toggle('labe-ativa')">Feminino</label>
                        </div>
                </div>
                <div class="card-lista">
                    <label class="input-label">endereco</label>
                    <input type="text" id="caixa-form" name="endereco" placeholder="input form"  v-model="form.endereco">			
                </div>

                <div class="card-lista lista-combos" style="">
                 <label class="input-label">Unidades</label>
                    <div class="input-box">
                        <label class="select-form">unidade</label>
                        <datalist id="dl_unidades">
                            <option value="falcon" data-value="1"></option>
                            <option value="f-15" data-value="2"></option>
                            <option value="sukoy" data-value="3"></option>
                            <option value="mig" data-value="4"></option>
                        </datalist>
                        <input list="dl_unidades"  class="input-form" id="input_unidade" v-model="form.unidade"/>
                        <!---->
                     </div>
                    <div class="input-box">
                         <label class="input-label">Cargo na unidade</label>
                        <label class="select-form">Cargo</label>
                        <datalist id="dl_cargo">
                            <option value="Diretor" data-value="1"></option>
                            <option value="Secretário" data-value="2"></option>
                            <option value="Capelão" data-value="3"></option>
                            <option value="Instrutor" data-value="4"></option>
                            <option value="Concelheiro" data-value="4"></option>
                        </datalist>
                        <input list="dl_cargo"  class="input-form" id="input_cargo" v-model="form.cargo"/>
                    </div>				
                </div>
              
                <div class="card-lista" v-if="false">
                    <label class="input-label btn btn-img" for="input-file" >imagem</label>
                    <input type="file" id="input-file" class="input-form" name="imagem" placeholder="input form" style="display: none;">			
                </div>

                <div class="card-lista-botoes">
                    <button @click="redirecionar" class="btn btn-cancel">voltar</button>
                    <button @click="cadastar(form)" class="btn btn-submit" id="formulario-btn" value="Enviar">Enviar</button>			
                    
                </div>
            </section>
        </main>
    </div>
</template>
<script>
    import percist from '@/store/percistencia_lideranca';
    import filtro from '../lideranca/BarraFiltro.vue';
    // require('../src/store/percistencia');
    export default {
        name: 'formCompo',
          data(){
                return {
                    n:'teste',
                    model_alergia:'',
                    model_doenca:'',
                    form:{
                        id:0,
                        nome:'',
                        idade:10,
                        sexo:'',
                        endereco:'',
                        imagem:'',
                        unidade:0,
                        especialidade:0,
                        cargo:0
                    }
                }
          },
          mounted(){
            this.listar();
          },
          components:{
            filtro
          },
          methods:{
                redirecionar(){
                    this.$router.push('/lideranca/listar');
                    // this.$router.go(-1);
                },
                listar(){

                    if(this.$route.params.id){
                        const id= this.$route.params.id;
                            let lista = percist.lista();
                            this.aux_hidratar(lista[id]);
                                console.log(lista[id]);
                    }
                    
                },
                cadastar(f){
                    
                    if(this.$route.params.id){
                        const id= this.$route.params.id;
                            percist.updateItem(f,id);
                            alert('Registro atualizado');
                             this.$router.push('/lista');
                    }else{
                        f.id=Math.floor(Math.random() * 10);
                        percist.salvar(f);
                    }
                    
                    
                        alert('concluido!');
                },
                addAlergia(a){
                    this.form.saude.alergias.push(a);
                },
                addDoenca(d){
                    this.form.saude.doencas_pre.push(d);
                },
                aux_hidratar(data){
                   console.log('data chegando e ',data);
                   this.form.nome = data.nome;
                    this.form.cargo = data.cargo;
                    this.form.especialidade = data.especialidade;
                    this.form.idade = data.idade;
                    this.form.endereco = data.endereco;
                    this.form.sexo = data.sexo;
                    this.form.especialidade = data.especialidade;
                    this.form.unidade = data.unidade;
                   
                }
          }
    }
</script>
<style lang="css"  src="../../../public/css/formulario.css">

</style>
<style lang="css"  scoped>
    #dl_unidades option{
        border:1px solid red;
        padding:1em;
    }
    .lista-combos{
        display: flex;
        flex-direction: column; 
        align-content: flex-start;
    }
    .select-form {
        outline:none;
        padding:12px 20px;
        border-radius:10px;
    }
    #caixa-form {
        outline:none;
        padding:12px 20px;
        border-radius:10px;
        box-shadow: 0 5px 20px rgba(0,0,0,0.05);
        border:1px solid #ccc;
    }
    .input-radio{
        opacity: 0;
    }
    /*lista doenças*/
    .lista-doencas
    {
        display: flex;
        flex-direction: column;
    }
    .lista-doencas > .inputBox{
        display: grid;
        grid-template-columns: 5fr 1fr;
        grid-gap: 12px;
        padding: 10px 10px;
    }
    .lista-doencas > .inputBox > .btn-add{
        cursor: pointer;
    }
    .lista-doencas > .marcadores{
        display: flex;
    }
    .lista-doencas > .marcadores > div{
        padding: 2px 3px;
        margin-right: 5px;
        border: 1px solid blue;
    }
    
    /*fim lista doenças*/
    /*css  label selecionado*/
        .labe-ativa{
            box-shadow: 1px 1px 10px blue;
        }
    /*fim label selecionado*/
</style>