<template>
    <div>
        <main>
            <filtro @pesqEvento="getPesquisaLista" />
            <section id="area-meio">
                
                <div class="card-lista" v-for="(dbv, index) in l_dbv" :key="index" >
                    <div class="img-card" @click="redirecionar(index)" >
                        <img :src="`${publicPath}/img/girl.png`">
                    </div>
                    <div class="info-card">
                        <span  @click="redirecionar(index)">{{dbv.nome}}</span>
                        <span>{{dbv.unidade}}</span>
                    </div>
                    <div class="acoes-card">
                        
                        <div class="btn-editar" @click="redirecionar(index)">
                            <img :src="`${publicPath}/icons/edit-circle.svg`">
                        </div>
                        
                        <div  class="btn-excluir"  @click="excluir(index)">
                            <img :src="`${publicPath}/icons/trash.svg`">
                        </div>
                    </div>				
                </div>
            </section>
        </main>
    </div>
</template>
<script>
/* eslint-disable */
 import db from '@/store/percistencia';
 import filtro from './BarraFiltro.vue';
    export default {
        name: 'ListaComp',
          data(){
                return {
                    n:'teste',
                    l_dbv:[{
                            id:0
                        }],
                        publicPath: process.env.BASE_URL
                }
          },
          components:{
            filtro
          },
          mounted(){
            this.listar();
          },
          methods:{
                redirecionar(id=null){
                    if(typeof id == 'number'){
                        this.$router.push(`/form_edt/${id}`);
                    }else{
                        this.$router.push(`/form`);
                    }
                        
                },
                listar(){
                    this.l_dbv = db.lista();
                },
                excluir(id){
                    db.removerItem(id);
                    alert(`item id:${id} foi excluido!`);
                    // this.$router.push(`/lista`);
                    this.listar();
                },
                getPesquisaLista(lista){
                    if(Array.isArray(lista)){
                            this.l_dbv = lista;
                    }else{
                     let lista_arr = [];
                      lista_arr.push(lista);
                       this.l_dbv = lista_arr;
                    }

                   
                }
          }
    }
</script>
<style lang="css"  src="../../public/css/lista.css"></style>
